var that;
import tuiSticky from "@/components/sticky/sticky"
export default {
	components:{
		tuiSticky
	},
	data() {
		return {
			fillwidth:false,
			scrollTop: 0,
			CustomBar: this.CustomBar,
			tabIndex: 1, //顶部筛选索引
			selectedName: ['地域', '排序', '阶段'],
			selectH1: 0,
			selectH2: 0,
			selectH3: 0,
			dropdownList1: [{
				value: 1,
				name: "地域1",
				selected: false
			}, {
				value: 2,
				name: "地域2",
				selected: false
			}, {
				value: 3,
				name: "地域3",
				selected: false
			}],
			dropdownList2: [{
				value: 0,
				name: "排序1",
				selected: false
			}, {
				value: 1,
				name: "排序2",
				selected: false
			}, {
				value: 2,
				name: "排序3",
				selected: false
			}],
			getTaskCategoryList: [],
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572526441738&di=72654ddd0b3a2628784e86cc540e4512&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fphotoblog%2F1707%2F28%2Fc6%2F54025678_1501245963871.jpg'
			}, {
				id: 1,
				type: 'image',
				url: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572526441738&di=72654ddd0b3a2628784e86cc540e4512&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fphotoblog%2F1707%2F28%2Fc6%2F54025678_1501245963871.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1572526441738&di=72654ddd0b3a2628784e86cc540e4512&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fphotoblog%2F1707%2F28%2Fc6%2F54025678_1501245963871.jpg'
			}],
			newsList: [
				"致力发展负责任的人工智能 中国发布八大治理原则",
				"格兰仕暗示拜访拼多多后遭天猫打压，拼多多高层赞其有勇气",
				"阿里计划将每股普通股拆为8股，增加筹资灵活性"
			],
			myList:["","","","","","","","","","","","","","",""]
		};
	},
	onPageScroll(e) {
		this.scrollTop= e.scrollTop
		if(e.scrollTop>300){
			this.fillwidth=true
		}else{
			this.fillwidth=false
		}
	},
	methods: {
		px(num) {
			return uni.upx2px(num) + "px"
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			// console.log(index)
			this.tabIndex = index;
			if (index == 1) {
				if (this.selectH1 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 2) {
				if (this.selectH2 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 3) {
				if (this.selectH3 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
		},
		showDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 400;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 400;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 300;
			}
		},
		hideDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
		},
	}
}
